<template>
  <div>
    <v-list-tile
      :key="item.title"
      avatar
      ripple
    >
      <v-list-tile-content>
        <v-list-tile-title>{{ item.name }}</v-list-tile-title>
        <v-list-tile-sub-title>{{ price }}</v-list-tile-sub-title>
        <v-list-tile-sub-title>{{ item.description }}</v-list-tile-sub-title>
      </v-list-tile-content>

      <v-list-tile-action>
        <v-list-tile-action-text>{{ duration }}</v-list-tile-action-text>
        <v-icon
          v-if="item.over18"
          color="grey lighten-1"
        >
          star_border
        </v-icon>

        <v-icon
          v-else
          color="yellow darken-2"
        >
          star
        </v-icon>
      </v-list-tile-action>

    </v-list-tile>
    <v-divider
    ></v-divider>
  </div>
</template>

<script>
export default {
  name: 'Activity',
  props: ['item'],
  computed: {
    duration () {
      var duration = (this.item.duration >= 60) ? this.item.duration / 60 : this.item.duration
      var interval = (this.item.duration / 60 >= 1) ? 'h' : 'm'

      return duration + interval
    },

    price () {
      if (this.item.price) {
        return '$' + parseFloat(Math.round(this.item.price * 100) / 100).toFixed(2)
      } else {
        return 'FREE'
      }
    }
  }
}
</script>



